.parallax {
    display: grid;
    grid-template-areas: "stack";
    min-height: 75vh;
}

.parallax > * {
    grid-area: stack;
    animation: parallax linear;
    animation-timeline: scroll();
}

.bg_img {
    background-image: url('../assets/prep.jpg');
    background-size: 150vh;
    background-repeat: no-repeat;
    --parallax-speed: 15;
    z-index: -10;
    height: 150vh;
    overflow: hidden;
}

.bg_color {
    position: fixed;
    z-index: -20;
    background: var(--color-gradient);
    width: 100vh;
    height: 100%;
}

@keyframes parallax {
    to {
        transform: translateY(calc(var(--parallax-speed) *  100px));
    }
}